.container{
    width: 607px;
    height: 700px;
    margin: auto;
    margin-top: 10px;
    margin-bottom: 70px;
    border-radius: 12px;
    background-image: linear-gradient(180deg,  #3e3b51  50%,#24142c  100%);
}
.top-bar{
    display: flex;
    justify-content: center;
    gap: 14px;
    padding-top: 40px;
}
.top-bar input{
    display: flex;
    width: 362px;
    height: 70px;
    background: #ebfffc;
    border: none;
    outline: none;
    border-radius: 40px;
    padding-left: 40px;
    color: #626262;
    font-size: 20px;
    font-weight: 400;
}
.search_icon{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 78px;
    height: 70px;
    background: #ebfffc;
    border-radius: 40px;
    cursor: pointer;

}
.weather-image{
    margin-top: 29px;
    display: flex;
    justify-content: center;
}
.weather-temp{
    display: flex;
    justify-content: center;
    color: white;
    font-size: 90px;
    font-weight: 400;
}

.weather-location{
    display: flex;
    justify-content: center;
    color: white;
    font-size: 50px;
    font-weight: 400;
}
.data-container{
    margin-top: 50px;
    color: white;
    display: flex;
    justify-content: center;
}
.element{
    margin: auto;
    display: flex;
    align-items: flex-start;
    gap: 12px;

}
.data{
    font-size: 25px;
    font-weight: 400;

}
.text{
    font-size: 15px;
    font-weight: 400;
}
.icon{
    margin-top: 10px;
    
}